import { Component, OnInit } from '@angular/core';
import {User} from "../../models/user";
import {FlashMessagesService} from "angular2-flash-messages";
import {RegisterService} from "../../services/register.service";
import {Router} from "@angular/router";


@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: []
})
export class RegisterComponent implements OnInit {

  user: User = {
    name: '',
    email: '',
    username: '',
    password: ''
  };

  constructor(
    private registerService: RegisterService,
    private router: Router,
    private flashService: FlashMessagesService) {
  }

  ngOnInit() {}

  onRegisterSubmit() {
    return this.registerService.register(this.user)
      .subscribe(data => {
        if(data.success) {
          this.flashService.show('You are now registered', { cssClass: 'alert-success', timeout: 3000 });
          this.router.navigate(['/login']);
        } else {
          this.flashService.show('failed to register', { cssClass: 'alert-danger', timeout: 3000 });
          this.router.navigate(['/register']);
        }
      });
  }
}



